import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { NgxWheelComponent, TextAlignment, TextOrientation } from 'ngx-wheel';
import { GuessWhoQuestion, ImageSource } from 'src/app/models/guess-who.model';
import { QuizAnswer, QuizQuestion } from 'src/app/models/quiz.model';
import { MmService } from 'src/app/services/mm.service';
import { MmImagePreviewComponent } from '../mm-image-preview/mm-image-preview.component';

@Component({
  selector: 'app-mm-home',
  templateUrl: './mm-home.component.html',
  styleUrls: ['./mm-home.component.css']
})
export class MmHomeComponent implements OnInit {
  @ViewChild('videoPlayer') videoPlayer: ElementRef;
  @ViewChild(NgxWheelComponent, { static: false }) wheel;

  isShowGuess = false;
  isShowStory = false;
  isShowAnwserhint = false;
  isShowAnswer = false;
  showEndVideo = false;
  guessWhoQ: GuessWhoQuestion[] = [];
  // currentQuestion: GuessWhoQuestion;  
  imageSourceAry: ImageSource[] = [];
  title = 'Happy Birthday Ats ðŸ˜';

  rightImage: ImageSource;
  leftImage: ImageSource;

  quizQ: QuizQuestion[] = [];
  currentQuestion: QuizQuestion;
  selectedAnswer: QuizAnswer;
  showCorrectAnswer = false;

  items: any[];
  idToLandOn: any;
  textOrientation: TextOrientation = TextOrientation.HORIZONTAL;
  textAlignment: TextAlignment = TextAlignment.CENTER;
  height: number = 380;
  width: number = 380;

  showMainPage = true;
  showTitle = false;
  showQuiz = false;
  showSpinningWheel = false;

  constructor(public dialog: MatDialog, private mmService: MmService) { }

  ngOnInit(): void {
  }

  onQuizStart(): void {
    this.quizQ = this.mmService.getQuizQ();
    this.showQuizPanel();
  }

  showQuizPanel(): void {
    if (this.quizQ.length > 0) {
      this.showMainPage = false;
      this.showTitle = true;
      this.showQuiz = true;
      this.showSpinningWheel = false;
      this.selectedAnswer = null;
      this.showCorrectAnswer = false;
      this.currentQuestion = this.quizQ.pop();
    } else {
      this.showMainPage = false;
      this.showTitle = false;
      this.showQuiz = false;
      this.showSpinningWheel = false;
      this.selectedAnswer = null;
      this.showCorrectAnswer = false;
      this.currentQuestion = null;
      this.showEndVideo = true;
      // TODOB: END
    }
  }

  onAnswerSelected(event): void {
    if (event && event.value) {
      this.selectedAnswer = event.value;
      this.showCorrectAnswer = true;
    }
  }

  showWheelPanel(): void {
    var body = document.body,
      html = document.documentElement;

    var height = Math.max(body.scrollHeight, body.offsetHeight,
      html.clientHeight, html.scrollHeight, html.offsetHeight);
    var width = Math.max(body.scrollWidth, body.offsetWidth,
      html.clientWidth, html.scrollWidth, html.offsetWidth);
    var dimension = width > height ? (height - 8) : (width - 8);

    if (dimension > 650) {
      dimension = (Math.floor(dimension / 100) * 100 - 75);
    }

    this.height = dimension;
    this.width = dimension;

    this.items = this.mmService.getDareList(dimension);
    this.idToLandOn = this.items[Math.floor(Math.random() * this.items.length)].id;

    this.showQuiz = false;
    this.showSpinningWheel = true;

    this.selectedAnswer = null;
    this.showCorrectAnswer = false;
  }

  reset() {
    this.wheel.reset();
  }

  before() {
  }

  async spin(prize) {
    this.idToLandOn = prize
    await new Promise(resolve => setTimeout(resolve, 0));
    this.wheel.spin()
  }

  after() {
    const selectedDare = this.items.find(x => x.id == this.idToLandOn)?.text
    const dialogRef = this.dialog.open(MmImagePreviewComponent, { data: selectedDare }); //, disableClose: true
    this.mmService.removeDare(selectedDare);
    
    dialogRef.afterClosed().subscribe(result => {
        this.showQuizPanel();
    });
  }

  startVideo(): void {
    this.videoPlayer.nativeElement.play();
  }
}
