import { Injectable } from '@angular/core';
import _quessWho from '../../assets/guessWho.json';
import _quizq from '../../assets/quizq.json';
import _darelist from '../../assets/darelist.json';
import { GuessWho, GuessWhoQuestion, ImageSource } from '../models/guess-who.model';
import { Quiz, QuizQuestion } from '../models/quiz.model';
import { utility } from '../utils/utility';

@Injectable({
  providedIn: 'root'
})
export class MmService {
  quizQ: Quiz = (_quizq as any);
  dareList: string[] = (_darelist as string[]);

  constructor() { }

  getQuizQ(): QuizQuestion[] {
    utility.shuffleArray(this.quizQ.tk);
    this.quizQ.tk.forEach(question => {
      utility.shuffleArray(question.answers);
    });
    utility.shuffleArray(this.quizQ.tk);
    return this.quizQ.tk;
  }

  getDareList(dimension: number): any[] {
    const seed = [...Array(this.dareList.length).keys()]
    utility.shuffleArray(seed);

    const maxCount = seed.length > 6 ? 6 : seed.length;
    const colors = ['#eae66f', '#88f26e', '#7de6ef'];

    const dareList = [];
    let colorIndex = 0;
    for (let index = 0; index < maxCount; index++) {
      if (++colorIndex == colors.length) {
        colorIndex = 0;
      }
      const displayText = this.dareList[seed[index]];
      dareList.push({
        fillStyle: colors[colorIndex],
        text: `${displayText}`,
        id: index,
        textFillStyle: 'black',
        textFontSize: dimension > 500 ? '17' : '10'
      });
    }
    return dareList;
  }

  removeDare(dare: any) {
    if (dare) {
      this.dareList = this.dareList.filter(obj => obj !== dare);
    }
  }

  quessWho: GuessWho = (_quessWho as any);
  getGuessWhoQ(): GuessWhoQuestion[] {
    utility.shuffleArray(this.quessWho.gw);
    this.quessWho.gw.forEach(question => {
      utility.shuffleArray(question.answers);
    });
    utility.shuffleArray(this.quessWho.gw);

    return this.quessWho.gw;
  }

  imageSourceAry: ImageSource[] = [];
  getImageList(): ImageSource[] {
    for (var index = 1; index < 60; index++) {
      this.imageSourceAry.push({ path: '../../../assets/bg_compressed/' + index + '.jpg' });
    }

    utility.shuffleArray(this.imageSourceAry);
    return this.imageSourceAry;
  }
}
